
import { GlobalContext } from "contents/MainFrame";
import { useContext } from "react";
import styled from "styled-components";

const ConfirmDialog = (props: {
    message: string,
    callback: () => void
}) => {
    const { state, dispatch, dispatcher } = useContext(GlobalContext);

    return (

        <_MainFrame>
            <_Message>{props.message}</_Message>
            <_Button
                onClick={() => {
                    dispatcher.closeDialog();
                }}
            >
                No
            </_Button>
            <_Button
                onClick={() => {
                    props.callback();
                }}
            >
                Yes
            </_Button>
        </_MainFrame>
    );
}

export default ConfirmDialog;

const _MainFrame = styled.div`
    display: inline-block;
    width: 250px;
    height: 100px;
    background-color: rgba(0, 0, 0, 0.842);
    border: solid 2px white;
`;

const _Message = styled.div`
    display:  inline-block;
    width: 100%;
    height: 50px;
    padding: 5px;
    font-size: 18px;
    font-weight: 600;
    /* line-height: -10px; */
    color: rgba(255, 255, 255, 0.856);
    text-align: left;
`;

const _Button = styled.div`
    display:  inline-block;
    width: 70px;
    height: 28px;
    margin: 4px 0 0 4px;
    background-color: rgb(180, 180, 180);
    border: solid 1px rgb(85, 85, 85);
    border-radius: 3px;
    font-size: 18px;
    font-weight: 600;
    line-height: 25px;
    color: rgba(0, 0, 0, 0.685);
    text-align: center;

    &:hover {
        background-color: rgb(139, 139, 139);
    }
`;