import './MainFrame.css';

import TabbedPane from 'contents/common/TabbedPane';
import {
    Route,
    Link,
    Switch,
    useLocation,
    useParams,
    BrowserRouter,
    HashRouter
} from 'react-router-dom'
import ScoreTab from 'contents/systab/score/ScoreTab';
import { initialState, Store, Action } from 'contents/redux/store';
import { ActionType, reducer } from 'contents/redux/reducer';
import { createContext, useReducer, KeyboardEvent, useEffect } from 'react';
import { keyboardShortCutAction } from './keyboad/KeyboardShortCutAction';
import { Dispatcher } from './redux/dispatcher';
import LoginFrame from './login/LoginFrame';
import UserPlate from './user/UserPlate';
import SamplerTab from './systab/sampler/SamplerTab';
import Viewer from './viewer/Viewer';


type GlobalContextType = {
    state: Store;
    dispatch: Function;
    dispatcher: Dispatcher;
}

export const GlobalContext = createContext({} as GlobalContextType);

interface RouterParams {
    id: string
}


const MainFrame = () => {

    const ROUTER_BASENAME = process.env.NODE_ENV === 'development' ? '/' : '/vh-react';
    return (
        // <BrowserRouter>
        <HashRouter basename={process.env.PUBLIC_URL}>
        {/* <BrowserRouter basename={ROUTER_BASENAME}> */}
            <Router />
        </HashRouter>
    );
}

const Router = () => {

    const [state, dispatchBase] = useReducer(reducer, initialState);
    const dispatch = (actionType: ActionType, payload: any) => dispatchBase({ type: actionType, payload: payload });
    const dispatcher = new Dispatcher(state, dispatchBase);

    const query = new URLSearchParams(useLocation().search);
    
    useEffect(()=> {
        const userAgent = window.navigator.userAgent.toLowerCase();
        alert(userAgent);
    }, []);

    return (
        <div
            id="main-frame"
            tabIndex={999}
            onKeyDown={(e: KeyboardEvent) => keyboardShortCutAction(e, state, dispatch, dispatcher)}
        >
            <GlobalContext.Provider value={{ state, dispatch, dispatcher }}>

                <Switch>
                    <Route path="/" exact>
                        {
                            !state.login.isLogin ?
                                <LoginFrame />
                                :
                                <>
                                    <UserPlate />
                                    <TabbedPane groupName="system-tabs"
                                        tabElements={[
                                            { name: 'Account', disable: true, cont: <div></div> },
                                            { name: 'Palette', disable: true, cont: <div></div> },
                                            { name: 'Score', disable: false, cont: <ScoreTab /> },
                                            { name: 'Backing', disable: true, cont: <div></div> },
                                            { name: 'Symbol', disable: true, cont: <div></div> },
                                            { name: 'Sampler', disable: false, cont: <SamplerTab /> },
                                            { name: 'Setting', disable: true, cont: <div></div> }
                                        ]}
                                        activeNo={2}
                                    />
                                </>
                        }
                        {((): JSX.Element => {
                            const dialog = state.dialog;
                            if (dialog.contents != null) {
                                return (
                                    <div className="dialog-cover">
                                        <div
                                            className="dialog"
                                            style={{ left: dialog.left, top: dialog.top }}
                                        >{dialog.contents}</div>
                                    </div>
                                )
                            }
                            return <></>;
                        })()}
                    </Route>
                    <Route path="/test" exact >
                        <Viewer id={'aa'}/>
                    </Route>
                    <Route path="/viewer" exact >
                        <Viewer id={query.get('v') as string}/>
                    </Route>
                </Switch>
            </GlobalContext.Provider>
        </div>
    );
}

export default MainFrame;
