import { Dispatcher } from "contents/redux/dispatcher";
import { BackingEditorState, BackingScript, BACKING_LEN_TYPE, ChordPaletteState, CHORD_MODE, DetailChange, DetailChord, DetailSection, EditMode, FloatWndState, Store, TypeNoteElement } from "contents/redux/store";
import { DIATONIC_MAJOR_SCALE_LIST, DIATONIC_MINOR_SCALE_LIST, KEY12_SHARP_LIST, SYMBOL_PARAMS_LIST } from "contents/common/define";
import { getSymbolParams } from "contents/common/utils";
import { ActionType } from "contents/redux/reducer";
import { KeyboardEvent } from 'react';
import { TuneDispatcher } from "contents/redux/store/FormDispatcher";
import { Player } from "soundfont-player";

/**
 * 
 * @param e 
 */
export const keyboadTuneTabOperation = (e: KeyboardEvent, state: Store, dispatch: Function, dispatcher: Dispatcher) => {

    // const action = state.formDispatcher.tune as TuneDispatcher;
    const tuneState = state.tuneState;
    const columnList = state.noteData.columnList;
    const curCache = tuneState.cacheList[tuneState.columnIndex];
    const curColumn = columnList[tuneState.columnIndex];
    const applyState = () => dispatcher.tune.setState({ ...tuneState });
    const applyColumn = () => dispatcher.tune.setColumnList(columnList.slice());

    const getSoundName = () => {
        const octave = Math.floor((87 + 9 - curColumn.soundIndex) / 12);
        const keyIndex = (87 + 9 - curColumn.soundIndex) % 12;
        const chordName = KEY12_SHARP_LIST[keyIndex];
        return chordName + octave;
    }

    switch (e.key) {
        case '1': {
            tuneState.usingLen = 16;
            applyState();
        }
            break;
        case '2': {
            tuneState.usingLen = 8;
            applyState();
        }
            break;
        case '3': {
            tuneState.usingLen = 4;
            applyState();
        }
            break;
        case '/': {
            tuneState.isDiv3 = !tuneState.isDiv3;
            applyState();
        }
            break;
        case ' ': {
            if (!tuneState.isPreview) {
                dispatcher.tune.playPreview();
                tuneState.isPreview = true;
                applyState();
            } else {
                dispatcher.tune.stopPreview();
                tuneState.isPreview = false;
                applyState();
            }
        }
            break;
        case 'w': {
            dispatcher.tune.adjustScrollX(tuneState.columnIndex);
        }
            break;
    }
    if (!tuneState.isEdit) {
        switch (e.key) {
            case 'ArrowLeft': {
                if (tuneState.columnIndex > 0) {
                    tuneState.columnIndex = tuneState.columnIndex -= 1;
                    applyState();
                    if (tuneState.isAutoScroll) {
                        dispatcher.tune.adjustScrollX(tuneState.columnIndex);
                        // dispatcher.tune.adjustScrollY(tuneState.columnIndex);
                    }
                }
            }
                break;
            case 'ArrowRight': {
                if (tuneState.columnIndex < columnList.length - 1) {
                    tuneState.columnIndex = tuneState.columnIndex += 1;
                    applyState();
                    if (tuneState.isAutoScroll) {
                        dispatcher.tune.adjustScrollX(tuneState.columnIndex);
                        // dispatcher.tune.adjustScrollY(tuneState.columnIndex);
                    }
                }
            }
                break;
            case 'Enter': {
                tuneState.isEdit = true;
                applyState();
            }
                break;
            case 'a': {
                dispatcher.tune.addColumn();
            }
                break;
            case 'Delete': {
                dispatcher.tune.remooveCur();
            }
                break;
        }
    } else {
        switch (e.key) {
            case 'ArrowUp': {
                let newIndex = curColumn.soundIndex;
                if (curColumn.soundIndex == -1) {
                    if (tuneState.columnIndex > 0) {
                        const prevSoundIndex = columnList[tuneState.columnIndex - 1].soundIndex;
                        if (prevSoundIndex !== -1) curColumn.soundIndex = prevSoundIndex;
                    } else {
                        curColumn.soundIndex = 44;
                    }
                } else if (curColumn.soundIndex > 0) {
                    do {
                        newIndex -= 1;
                        const isAccept = [0, 2, 4, 5, 7, 9, 11].includes((curCache.baseKey + newIndex + 4) % 12);
                        if (isAccept) {
                            curColumn.soundIndex = newIndex;
                            break;
                        }
                    } while (newIndex > 0);
                }
                if (tuneState.isAutoScroll) {
                    dispatcher.tune.adjustScrollY(tuneState.columnIndex);
                }
                applyColumn();
                dispatcher.tune.playSoundFont(getSoundName(), 0.5);
            }
                break;
            case 'ArrowDown': {
                let newIndex = curColumn.soundIndex;
                if (curColumn.soundIndex == -1) {
                    if (tuneState.columnIndex > 0) {
                        const prevSoundIndex = columnList[tuneState.columnIndex - 1].soundIndex;
                        if (prevSoundIndex !== -1) curColumn.soundIndex = prevSoundIndex;
                    } else {
                        curColumn.soundIndex = 44;
                    }
                } else if (curColumn.soundIndex < 87) {
                    do {
                        newIndex += 1;
                        const isAccept = [0, 2, 4, 5, 7, 9, 11].includes((curCache.baseKey + newIndex + 4) % 12);
                        if (isAccept) {
                            curColumn.soundIndex = newIndex;
                            break;
                        }
                    } while (newIndex < 87);
                }
                if (tuneState.isAutoScroll) {
                    dispatcher.tune.adjustScrollY(tuneState.columnIndex);
                }
                applyColumn();
                dispatcher.tune.playSoundFont(getSoundName(), 0.5);
            }
                break;
            case 'ArrowLeft': {
                dispatcher.tune.adjustWidth(false);
            }
                break;
            case 'ArrowRight': {
                dispatcher.tune.adjustWidth(true);
            }
                break;
            case 'Enter': {
                tuneState.isEdit = false;
                applyState();
            }
                break;
            case 'Delete': {
                curColumn.soundIndex = -1;
                applyColumn();
            }
                break;
            case 'a': {
                dispatcher.tune.addColumn();
                tuneState.columnIndex = tuneState.columnIndex += 1;
                applyState();
            }
                break;
        }

    }
}