import { KEY12_SHARP_LIST } from "contents/common/define"
import { GlobalContext } from 'contents/MainFrame';
import { useContext, useEffect, useState } from 'react';
import './SamplerTab.css';

type SampleRecord = {
    // 
    soundName: string,
    // 
    isUse: boolean,
    // URL
    url: string
};

const SamplerTab = () => {
    const { state, dispatcher } = useContext(GlobalContext);

    const [samples, setSamples] = useState<SampleRecord[]>();

    useEffect(() => {

        const samples: SampleRecord[] = [];

        for (let i = 0; i <= 88; i++) {
            samples.push(
                {
                    soundName: KEY12_SHARP_LIST[(i + 9) % 12] + Math.floor((i + 9) / 12),
                    isUse: false,
                    url: ""
                },
            );
        }
        setSamples(samples);
    }, []);

    const createRecoredJsxList = (): JSX.Element[] => {
        const list: JSX.Element[] = [];
        samples?.forEach((sample, i)=> {
            list.push(
                <div key={i} className="record">
                    <div className="label"><span>{sample.soundName}</span></div>
                    <input type="checkbox"
                        checked={sample.isUse}
                        onChange={e => {
                            sample.isUse = e.target.checked;
                            setSamples(samples.slice());
                        }}
                    />
                    <input
                        type="text"
                        className={!sample.isUse ? 'disable' : ''}
                        value={sample.url}
                        onChange={e => {
                            sample.url = e.target.value;
                            setSamples(samples.slice());
                        }} />
                </div>
            );
        });
        return list;
    }

    const getSamplesList = () => {
        let samplesList = {};
        for (let i = 0; i < 88; i++) {
            if (samples && samples[i]) {
                if (samples[i].isUse) {
                    samplesList = { [samples[i].soundName]: samples[i].url };
                }
            }
        }
        return samplesList;
    }

    return (
        <div id="samplertab-root">
            // div
            <div className="loader"></div>

            // div
            <div className="list">
                {createRecoredJsxList()}
            </div>

            // div
            <div className="operation">
                <button
                    className={[
                        'disable'
                    ].join(' ')}
                    onClick={() => {

                    }}>Save</button>

                <button
                    className={[
                        'disable'
                    ].join(' ')}
                    onClick={() => {

                    }}>Reset</button>

                <button onClick={() => {
                    dispatcher.setSampler(getSamplesList());
                }}>Build</button>
            </div>
            <div className="keyboad"></div>
        </div>
    );
}

export default SamplerTab;