
import Soundfont from 'soundfont-player';
import { GlobalContext } from 'contents/MainFrame';
import { ActionType } from 'contents/redux/reducer';
import { DetailChord, FloatWndState } from 'contents/redux/store';
import { useContext, useEffect, useRef, useState } from 'react';
import styled from 'styled-components';
import './ThemaLoader.css';

interface PatternBean {
    name: string;

}

const ThemaLoader = () => {

    const { state, dispatch, dispatcher } = useContext(GlobalContext);

    const [patterns, setPatterns] = useState<PatternBean[]>([]);

    // const ref = useRef({} as HTMLDivElement);
    // useEffect(() => {
    //     dispatch(ActionType.GET_REFFER_SECTION_PALETTE, { reffer: ref.current });
    // }, []);

    useEffect(() => {
        const patterns: PatternBean[] = [];

        if (state.noteState.floatwndState === FloatWndState.THEMA_ACCESSOR) {
            const bean = state.dinamicCache as DetailChord;

            for (let sus of state.themaDataTree.sustains) {
                const isSustainMatch = sus.beat === bean.sustain && sus.headAdjust === bean.headAdjust && sus.tailAdjust === bean.tailAdjust;
                if (isSustainMatch) {

                    sus.patterns.forEach((pattern) => {
                        patterns.push({
                            name: pattern.name,
                        });
                    });
                    break;
                }
            }
            setPatterns(patterns);
            Soundfont.instrument(new AudioContext(), 'violin', {gain: 4, release: 0.5}).then((instrument) => {
                dispatcher.setInstruments(instrument);
            });
        }
    }, [state.noteState.floatwndState]);

    const instrument = state.instrument as Soundfont.Player;

    let operation: JSX.Element = <></>;
    operation = (<>
        <div
            onClick={() => {
                dispatch(ActionType.FLOATWND_STATE, { state: FloatWndState.NONE });
                instrument.stop();
            }}>
            <span>Cancel</span>
        </div>
        <div
            onClick={() => {
                // instrument.play('C4', 0).stop(5);
                
                // instrument.start('C4', 2);
                // const nodes: AudioNode[] = [];
                // instrument.stop(1000);
                instrument.schedule(0, [
                    { time: 0, note: 60, duration: 2 },
                    { time: 2.5, note: 63, duration: 2 },
                ]);

            }}>
            <span>Enter</span>
        </div>
    </>);

    const itemJsxList: JSX.Element[] = [];
    patterns.forEach((pattern, i) => {
        itemJsxList.push(
            <_Item key={i}>{pattern.name}</_Item>
        );
    });
    return (

        <div
            id="thema-accessor"
            className={'floatwnd' + (state.noteState.floatwndState == FloatWndState.THEMA_ACCESSOR ? ' active' : '')}
            onClick={() => {
            }}
        >
            <_Wrap>
                <_WrapList>
                    {itemJsxList}
                </_WrapList>
                <_WrapMatrix>

                </_WrapMatrix>
            </_Wrap>
            <div className="operation">
                {operation}
            </div>
        </div>
    );
}

export default ThemaLoader;


const _Wrap = styled.div`
    display: inline-block;
    height: 250px;
`;

const _WrapList = styled.div`
    display: inline-block;
    width: 280px;
    height: 100%;
    background-color: #000000;
    vertical-align: top;
    overflow-y: auto;
`;
const _Item = styled.div`
    display: inline-block;
    width: 100%;
    height: 30px;
    background-color: #272727;
    overflow-x: hidden;
    border: solid 1px #b4b4b4;
    font-size: 20px;
    font-weight: 600;
    color: #ffffff;
    line-height: 25px;
    padding-left: 10px;
    box-sizing: border-box;
`;

const _WrapMatrix = styled.div`
    display: inline-block;
    width: 280px;
    height: 100%;
    background-color: #868686;
    vertical-align: top;
    overflow-y: auto;
`;
