
import TabbedPane from 'contents/common/TabbedPane';
import FooterTiming from './timing/FooterTiming';
import './NoteFooter.css';
import FooterEnsemble from './ensemble/FooterEnsemble';
import { useContext, useEffect, useState } from 'react';
import { GlobalContext } from 'contents/MainFrame';
import { TypeNoteElement } from 'contents/redux/store';

const NoteFooter = () => {

    const { state, dispatcher } = useContext(GlobalContext);
    const curType = state.noteData.dataList[state.noteState.focusIndex].type;

    const [activeTabIndex, setActiveTabIndex] = useState<number>(0);

    useEffect(()=>{
        if(curType) {
            setActiveTabIndex(curType !== TypeNoteElement.Init ? 0 : 2);
        }
    }, [state.noteState.focusIndex]);
    return (
        <div id="score-footer">
            <TabbedPane groupName="score-tabs"
                tabElements={[
                    { name: 'Progress', disable: true, cont: <FooterTiming /> },
                    { name: 'Target', disable: true, cont: <div></div> },
                    { name: 'Ensemble', disable: true, cont: <FooterEnsemble /> }
                ]}
                activeNo={activeTabIndex}
            // selectTabIndex={(index: number) => dispatch(ActionType.SELECT_SCORETAB, { index: index })}
            />
        </div>
    );
}

export default NoteFooter;